* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e1dddd;
  background-image: linear-gradient(#9e9494, #e1dddd);
  background-repeat: no-repeat;
  background-size: contain;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-height: 100%;
  width: auto;
  display: block;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.descr-container-title {
  margin-top: auto;
}

.error {
  color: red;
  font-weight: 500;
}

.loader {
  display: flex;
}

.loading {
  width: 100px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: flex;
  position: relative;
}

.loading:after, .loading:before {
  content: "";
  width: 34px;
  height: 24px;
  background-color: green;
  background-image: radial-gradient(8px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
  background-repeat: no-repeat;
  border-radius: 50%;
  animation: eyeMove 5s infinite, blink 10s infinite;
  display: inline-block;
}

@keyframes eyeMove {
  0%, 2% {
    background-image: radial-gradient(8px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
    background-position: 0 0;
  }

  5%, 22% {
    background-image: radial-gradient(8px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
    background-position: 12px 0;
  }

  23%, 40% {
    background-image: radial-gradient(8px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
    background-position: 10px 0;
  }

  43%, 70% {
    background-image: radial-gradient(8px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
    background-position: -10px 0;
  }

  73%, 90% {
    background-image: radial-gradient(8px, #0d161b 100%, rgba(0, 0, 0, 0) 0);
    background-position: 0 10px;
  }

  93%, 100% {
    background-image: linear-gradient(to right, green, #0d161b, green);
    background-position: 0 0;
  }
}

@keyframes blink {
  0%, 10%, 12%, 20%, 22%, 40%, 42%, 60%, 62%, 70%, 72%, 90%, 92%, 98%, 100% {
    height: 24px;
  }

  11%, 21%, 41%, 61%, 71%, 91%, 99% {
    height: 9px;
  }
}

